.weatherBackground {
    margin: 0;
    font-family: Arial, sans-serif;
    background-image: url("https://tse2.mm.bing.net/th?id=OIP.nbrT5M-mreFzz990rig_SQHaE7&pid=Api&P=0&h=220");
    background-size: cover;
    background-repeat: no-repeat;
    height: 100vh;
}

.app {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    text-align: center;
}

.weather-container {
    padding: 2rem;
    border-radius: 20px;
    text-align: center;
    width: 90%;
    max-width: 450px;
    margin-top: 20px;
    background-color: rgba(255, 255, 255, 0.1);
    backdrop-filter: blur(30px);
    box-shadow: 10px 10px 10px rgba(0, 0, 0, 0.1), -2px -2px 2px rgba(255, 255, 255, 0.1);
    transition: all 0.3s ease;
}

.search-box {
    margin-bottom: 20px;
}

.search-box input {
    padding: 10px;
    font-size: 16px;
    border: none;
    border-radius: 5px;
    margin-right: 10px;
}

.search-box button {
    padding: 10px;
    font-size: 16px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    background: hsl(234, 100%, 50%);
    color: #fff;
    transition: background 0.3s ease;
}

.search-box button:hover {
    background: hsl(264, 100%, 45%);
}

.cool {
    background-image: url("https://wallpapers.com/images/hd/weather-background-dnp22r4ifxzgvl8c.jpg");
    background-size: cover;
    background-repeat: no-repeat;
    height: 100vh;
}

.moderate {
    background-image: url("https://www.pixelstalk.net/wp-content/uploads/2016/07/Weather-Images-HD.jpg");
    background-size: cover;
    background-repeat: no-repeat;
    height: 100vh;
}

.hot {
    background-image: url("https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcS_d5n9hBfwkXaCEGK24lxmKX_ge7kBU1EpHA&s");
    background-size: cover;
    background-repeat: no-repeat;
    height: 100vh;
}

.rain {
    background-image: url("https://wallpapercave.com/wp/xREMNH6.jpg");
    background-size: cover;
    background-repeat: no-repeat;
    height: 100vh;
}

.weather-info {
    margin-top: 20px;
}

.forecast-container {
    margin-top: 20px;
}

.forecast-row {
    display: flex;
    justify-content: center;
    gap: 20px;
    margin-bottom: 20px;
}

.forecast-day {
    padding: 10px;
    border-radius: 8px;
    background-color: rgba(255, 255, 255, 0.2);
    text-align: center;
    width: 100px;
}

.forecast-day p {
    margin: 5px 0;
}
